export function trimContent(str, maxLength) {
  let trimmedString = str
    .replace(/(\r\n|\n|\r)/gm, ' ')
    .replace(/ +(?= )/g, '')
    .trim();

  if (trimmedString.length > maxLength) {
    trimmedString = trimmedString.substr(0, maxLength);
    trimmedString = trimmedString.substr(
      0,
      Math.min(trimmedString.length, trimmedString.lastIndexOf(' '))
    );
  }

  return trimmedString;
}

export function trimDescription(str) {
  return trimContent(str, 300) + '.';
}

export function webViewHtml(code) {
  return `<html>
            <head>
              <meta name="viewport" content="width=device-width, initial-scale=1.0" />
            </head>
            <body>
              ${code}
            </body>
          </html>`;
}

function findAsset(body) {
  return body.content.find((elem) => elem.nodeType === 'embedded-asset-block');
}

export function findImage(body) {
  const asset = findAsset(body);
  return asset ? asset.data.target.fields : false;
}

export function hexToRgba(hex, alpha = 1) {
  let c;
  if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
    c = hex.substring(1).split('');
    if (c.length == 3) {
      c = [c[0], c[0], c[1], c[1], c[2], c[2]];
    }
    c = '0x' + c.join('');
    return (
      'rgba(' +
      [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') +
      ',' +
      alpha +
      ')'
    );
  }
  throw new Error('Bad Hex');
}

export function getContentTypeByPostType(postType) {
  switch (postType) {
    case 'info':
      return Constants.manifest.extra.CONTENT_TYPES.INFO;

    default:
      return Constants.manifest.extra.CONTENT_TYPES.BLOG;
  }
}
