import Head from 'next/head';
import Error from 'next/error';
import { withRouter } from 'next/router';
import { StyleSheet, View } from 'react-native';
import { Card, Divider, Text } from 'react-native-elements';
import { useTranslation } from 'react-i18next';
import { documentToPlainTextString } from '@contentful/rich-text-plain-text-renderer';
import path from 'path';
import client from '../../../src/apis/web/contentfulClient';
import { trimDescription, getContentTypeByPostType } from '../../../src/utils';
import ContentfulRichText from '../../../src/utils/contentful/ContentfulRichText';

import Styles from '../../../constants/Styles';
import Colors from '../../../constants/Colors';

const Post = ({ item }) => {
  if (!item) return <Error statusCode={404} />;

  const { t } = useTranslation(['post', 'app']);
  const created = new Date(item.sys.createdAt).toDateString();
  const updated = new Date(item.sys.updatedAt).toDateString();
  const { title, body } = item.fields;

  return (
    <View style={Styles.container}>
      <Head>
        <title>
          {title} - {t('app:name')}
        </title>
        <meta
          name="description"
          content={trimDescription(documentToPlainTextString(body))}
        />
      </Head>

      <Card
        title={
          <View style={styles.titleView}>
            <Text h1 style={styles.title}>
              {title}
            </Text>
            <View style={styles.dates}>
              <Text style={styles.date}>
                {t('post:created')}
                {created}
              </Text>
              <Text style={styles.dateSeparator}>|</Text>
              <Text style={styles.date}>
                {t('post:updated')}
                {updated}
              </Text>
            </View>
            <Divider />
          </View>
        }
        containerStyle={styles.cardContainer}
      >
        <View style={styles.bodyView}>{ContentfulRichText(body)}</View>
      </Card>
    </View>
  );
};

Post.getInitialProps = async function ({ query, pathname }) {
  const { slug } = query;
  const postType = path.basename(path.dirname(pathname));

  const entries = await client.getEntries({
    content_type: getContentTypeByPostType(postType),
    'fields.slug[in]': slug,
  });

  return { item: entries.items[0] };
};

export default withRouter(Post);

const styles = StyleSheet.create({
  cardContainer: { margin: 0 },
  titleView: {
    paddingVertical: 10,
    paddingHorizontal: 10,
  },
  title: {
    fontWeight: '700',
    lineHeight: 35,
    paddingBottom: 15,
  },
  dates: { flexDirection: 'row', paddingBottom: 20 },
  date: { color: Colors.secondaryText },
  dateSeparator: { paddingHorizontal: 8 },
  bodyView: { marginTop: 10, marginBottom: 100 },
});
